import comSwiper from '../../components/comSwiper/comSwiper.vue';
import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue";
export default {
    data () {
      return {
		  number:[],
		  total:'0.00',
		  isNumber:false,
		 checkedArr:[], //复选框选中的值
		 allChecked:false, //是否全选
		  catList:[
			  {
				  name:"小米10探索版258G",
				  img:"../../static/logo.png",
				  specification:"高配",
				  price:36990.88,
				  quantity:1,
				  status:0,
				  value:0
			  },
			  {
				  name:"小米11探索版520G",
				  img:"../../static/logo.png",
				  specification:"高配",
				  price:56870.88,
				  quantity:1,
				  status:0,
				  value:1
			  },
			  {
				  name:"小米12探索版1024G",
				  img:"../../static/logo.png",
				  specification:"高配",
				  price:99990.99,
				  quantity:1,
				  status:0,
				  value:2
			  }
		  ]
      }
    },
	components:{
		comSwiper,
		uniNumberBox
	},
	created() {
		// this.catList.forEach((item)=>{
		// 	item.isChecked = true
		// })
	},
    methods: {
		returnFloat(value){
		 var value=Math.round(parseFloat(value)*100)/100;
		 var xsd=value.toString().split(".");
		 if(xsd.length==1){
		 value=value.toString()+".00";
		 return value;
		 }
		 if(xsd.length>1){
		 if(xsd[1].length<2){
		 value=value.toString()+"0";
		 }
		 return value;
		 }
		},
		
		calcTotal(){
			this.total = 0;
			let total = 0;
			this.catList.forEach((item) => {
				if(item.status === 1){
					total  += (item.price*100*item.quantity)/100
				}
			})
			this.total = this.returnFloat(total) 
			console.log(this.total)
		},
		
		// 多选复选框改变事件
		changeCheckbox(e){
				console.log(e)
			    this.checkedArr = e.detail.value;
				console.log(this.checkedArr);
				if(this.checkedArr.length>0){
					this.catList.forEach((item) =>{
						item.status = 0
					})
					this.checkedArr.forEach((item) => {
						this.catList[item].status = 1
					})
					this.calcTotal()
				}else if(this.checkedArr.length === 0){
					this.total = 0;
					this.total = '0.00'
				}
			    // 如果选择的数组中有值，并且长度等于列表的长度，就是全选
			    if(this.checkedArr.length>0 && this.checkedArr.length==this.catList.length){
			        this.allChecked=true;
			    }else{
			        this.allChecked=false;
			    }
		},
		// 全选事件
		allChoose(e){
		    let chooseItem = e.detail.value;
		    // 全选
			this.total = 0;
			
			console.log(e)
			console.log(chooseItem)
		    if(chooseItem[0]=='all'){
		        this.allChecked=true;
				let quellTotal = 0
		        for(let item of this.catList){
		            let itemVal=String(item.value);
					
					quellTotal += (item.price*100*item.quantity)/100
					
					
					console.log(itemVal)
		            if(!this.checkedArr.includes(itemVal)){
		                this.checkedArr.push(itemVal);
		            }
		        } 
				this.total = this.returnFloat(quellTotal);
			   console.log(this.total)
		    }else{
		        // 取消全选
		        this.allChecked=false;
		        this.checkedArr=[];
				this.total = '0.00';
		    }
		},
		
		bindChange(e , index){
			
			  this.catList[index].quantity = e
			  this.calcTotal();
			  
		}
	
    }
}
